body {
  margin: 0;
  padding: 0;
  background-color: #f2f4f5;
  color: #1e1e1f;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#root {
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}